import { Component, OnInit, ViewChild } from '@angular/core';
import { ActionSheetController, PopoverController, IonInfiniteScroll } from '@ionic/angular';
import { TeamsMenuComponent } from './teams-menu/teams-menu.component';
import { User } from 'src/models/User';
import { Team } from 'src/models/Team';
import { Utils } from 'src/commons/Utils';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { ToastText, MenuText, UserType } from 'src/commons/Constants';
import { PageDataService } from 'src/providers/page-data/page-data.service';
import { AuthenticationService } from 'src/app/services/authentication.service';

@Component({
  selector: 'app-teams',
  templateUrl: './teams.page.html',
  styleUrls: ['./teams.page.scss'],
})
export class TeamsPage {


  @ViewChild(IonInfiniteScroll) infiniteScroll: IonInfiniteScroll;


  private readonly logHeader = "TeamsPage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//
  
  public pending: boolean = false;
  public teams: Array<Team> = new Array<Team>();
  public team: Team;
  private teamLeader: User;
  private users: Array<User> = new Array<User>();
  public userList: Array<User> = new Array<User>();
  private searchTerm: string;
  public user: User;
  public imageConcatString = Utils.generateRandomString();

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(public actionSheetController: ActionSheetController,
    public popoverController: PopoverController,
    private api: BackendApiProvider,
    private utils: Utils,
    private pageDataService: PageDataService,
    private auth: AuthenticationService) {
    const logHeader = this.logHeader + "constructor>>";
    Utils.log(logHeader);
    this.user = this.auth.loggedUser;
    this.team = new Team();
    this.api.getUserById(this.auth.loggedUser.getId()).then((user: User) => {
      this.auth.setAuthObject(user);
    })

    this.getTeams();
    if (!Utils.isVarInvalid(this.auth.loggedUser.getEquipeId())) {
      this.getTeamById(this.auth.loggedUser.getEquipeId());
    }
    this.pageDataService.listUserEvent.subscribe(data => {
      this.getSelectedUsers();
    });

  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getTeamById(input: number) {
    this.api.getTeamById(input).then((team: Team) => {
      this.team = team;
      this.users = this.team.getMembros();
      this.userList = this.users;
      this.isPending();
      this.api.getUserById(team.getLiderId()).then((leader: User) => {
        this.imageConcatString = Utils.generateRandomString();
        this.teamLeader = leader;
      }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getTeams() {
    this.api.getTeams().then((teams: Array<Team>) => {
      if (!Utils.isArrayInvalid(teams)) {
        this.teams = teams;
      }
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async abrirActionSheet(user: User) {
    const actionSheet = await this.actionSheetController.create({
      header: MenuText.WHAT_DO_YOU_WANT_TO_DO,
      buttons: [{
        text: MenuText.REMOVE_FROM_TEAM,
        role: 'destructive',
        handler: () => {
          this.users.splice(this.users.indexOf(user), 1);
          this.team.setMembros(this.users);
          this.filter(this.searchTerm);
        }
      },
      {
        text: MenuText.CANCEL, role: 'cancel',
        handler: () => { }
      }]
    });
    await actionSheet.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  filter(evt) {
    const logHeader = this.logHeader + "filter>>";
    Utils.log(logHeader);
    if (Utils.isVarInvalid(evt)) {
      this.userList = this.users;
      return;
    }
    const searchTerm = evt;
    this.searchTerm = searchTerm;
    if (!searchTerm) {
      this.userList = this.users;
      return;
    }
    this.userList = this.users.filter(n => n.getNome().toLowerCase().includes(searchTerm.toLowerCase()) || n.getEmail().toLowerCase().includes(searchTerm.toLowerCase()));
  }
  //-------------------------------------------------------------------------------------------------------------------------------//

  async openMenu(ev: any) {
    this.DismissClick();
    this.pageDataService.setData('teamData', this.team);
    const popover = await this.popoverController.create({
      component: TeamsMenuComponent,
      event: ev,
      animated: true,
      showBackdrop: true,
      translucent: true,
      cssClass: 'popover_class',
      componentProps: {
        team: this.team,
        teams: this.teams
      }
    });
    await popover.present();
    const { data } = await popover.onDidDismiss();
    if (!Utils.isVarInvalid(data.team) && !Utils.isVarInvalid(data.leader)) {
      this.team = data.team;
      this.teamLeader = data.leader;
      this.users = this.team.getMembros();
      this.filter(this.searchTerm);
    }
  }

  async DismissClick() {
    await this.popoverController.dismiss();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  editTeam(successMessage: string) {
    if (!this.auth.loggedUser.isAdmin() && this.isLeader()) {
      this.api.leaderEditTeam(this.team).then((team: Team) => {
        this.utils.simpleToast(successMessage);
        this.getTeamById(team.getId());
        this.filter(this.searchTerm);
      }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
    }
    else if (this.auth.loggedUser.isAdmin()) {
      this.api.adminEditTeam(this.team).then((team: Team) => {
        this.utils.simpleToast(successMessage);
        this.getTeamById(team.getId());
        this.filter(this.searchTerm);
      }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
    }

  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getSelectedUsers() {
    let data = this.pageDataService.getData('selectedUsers');
    this.pageDataService.removeData('selectedUsers');
    if (!Utils.isArrayInvalid(data)) {
      for (let userTemp of data) {
        let user = userTemp as User;
        if (this.users.indexOf(user) < 0) {
          this.users.push(user);
        }
      }
      this.team.setMembros(this.users);
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  save() {
    this.getSelectedUsers();
    this.editTeam(ToastText.EDIT_SUCCESSFUL);
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  teamSelected(id: number) {
    this.getTeamById(id);
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  joinRequest() {
    this.api.joinRequest(this.team.getId()).then(() => {
      this.utils.simpleToast(ToastText.REQUEST_SUCCESSFUL + '\n' + ToastText.WAIT_FOR_APPROVAL);
      this.isPending();
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG))
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  isLeader() {
    return this.auth.loggedUser.getId() == this.team.getLiderId();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  isPending() {
    this.api.isWaitingApproval(this.team.getId()).then((response) => this.pending = response).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  showJoinRequest() {
    return this.users.filter(n => n.getId() == this.auth.loggedUser.getId()).length == 0 && !this.isLeader();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  loadData(event) {
    setTimeout(() => {
      console.log('Done');
      event.target.complete();
      this.imageConcatString = Utils.generateRandomString();
      if (this.userList.length == 1000) {
        event.target.disabled = true;
      }
    }, 500);
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  isInTeam() {
    return this.user.getEquipeId() != null && this.user.getEquipeId() != undefined;
  }

}
